  color: #363636;
  line-height: 1.5;
  overflow-x: hidden;
  position: relative;
  width: 100%;
}
@media (min-width: 1200px) {
  body {
    font-size: 18px;
  }
}
a {
  color: #336699;
  text-decoration: underline;
}
a:hover,
a:focus {
  color: #264c73;
}
figure {
  margin: 0;
  background-size: auto 100%;
  background-position: center center;
  background-repeat: no-repeat;
}
figure img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
  margin: 0 auto;
  transition: opacity 0.2s;
}
figure.contain-width img {
  width: auto;
}
