/* Components
********************/
.header {
  height: 15em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  background-color: #f42d40;
  background-image: linear-gradient(30deg, #A70E1D 12%, transparent 12.5%, transparent 87%, #A70E1D 87.5%, #A70E1D), linear-gradient(150deg, #A70E1D 12%, transparent 12.5%, transparent 87%, #A70E1D 87.5%, #A70E1D), linear-gradient(30deg, #A70E1D 12%, transparent 12.5%, transparent 87%, #A70E1D 87.5%, #A70E1D), linear-gradient(150deg, #A70E1D 12%, transparent 12.5%, transparent 87%, #A70E1D 87.5%, #A70E1D), linear-gradient(60deg, #FF495A 25%, transparent 25.5%, transparent 75%, #FF495A 75%, #FF495A), linear-gradient(60deg, #FF495A 25%, transparent 25.5%, transparent 75%, #FF495A 75%, #FF495A);
  background-size: 80px 140px;
  background-position: 0 0, 0 0, 40px 70px, 40px 70px, 0 0, 40px 70px;
}
.header__heading1 {
  color: #ffffff;
}
.header__heading3 {
  color: #ffffff;
}
.container {
  position: relative;
  width: 100%;
  min-height: 100vh;
  display: flex;
  background-color: #acacac;
  box-shadow: 0px -20px 50px -10px rgba(0, 0, 0, 0.5);
}
/**
Filter tab area
 */
.filter {
  display: flex;
  list-style: none;
  flex-direction: column;
  width: 5em;
  padding: 0;
  margin: 0;
  border: none;
}
@media (min-width: 992px) {
  .filter {
    width: 10em;
  }
}
.filter__tab {
  width: 100%;
  height: 5em;
  text-align: center;
  margin: 0;
  background-color: #acacac;
}
.filter__tab--active {
  transition: background-color 0.5s ease;
  background-color: #f6f8fB;
  box-shadow: 0px 10px 50px 0px rgba(0, 0, 0, 0.5);
  z-index: 8;
}
.filter__icon {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  padding: 10px 0;
  border-bottom: 1px solid #7f7f7f;
  z-index: 10;
  position: relative;
  text-decoration: none;
}
@media (min-width: 992px) {
  .filter__icon {
    padding: 20px 0;
  }
}
.filter__input {
  visibility: hidden;
}
/**
Filter content area
 */
.content-wrapper {
  display: flex;
  flex-direction: row;
  width: 100%;
  position: relative;
  z-index: 9;
}
.tab-content {
  display: none;
  background-color: #f6f8fB;
  width: 100%;
  padding: 30px;
  position: relative;
  z-index: 11;
}
.tab-content--active {
  display: block;
}
.repo-list {
  padding: 0;
  margin: 0 -15px;
  position: relative;
  display: flex;
  flex-flow: row wrap;
}
.repo {
  padding: 0 15px;
  width: 100%;
  margin-bottom: 30px;
  list-style: none;
}
@media (min-width: 992px) {
  .repo {
    width: 50%;
  }
}
@media (min-width: 1200px) {
  .repo {
    width: 25%;
  }
}
.repo--theme- .repo__heading {
  background-color: #4477D7;
  color: white;
}
.repo--theme-JavaScript .repo__heading {
  background-color: #F1E05A;
  color: white;
}
.repo--theme-CSS .repo__heading {
  background-color: #563D7C;
  color: white;
}
.repo--theme-HTML .repo__heading {
  background-color: #E34C26;
  color: white;
}
.repo__link {
  background-color: #f6f8fB;
  display: block;
  width: 100%;
  text-decoration: none;
}
.repo__link:hover {
  transition: box-shadow 0.5s ease;
  box-shadow: 0px 0px 50px 0px rgba(0, 0, 0, 0.25);
}
.repo__heading {
  background-color: #ebedf0;
  margin: 0;
  padding: 5% 0 5% 5%;
  font-weight: 400;
}
.repo__description {
  background-color: #ffffff;
  margin: 0;
  padding: 5% 0 5% 5%;
}
/* Keyframes for the fade-in */
@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@-moz-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.fade-in {
  opacity: 0;
  opacity: 1 \9;
  /*just in case ie*/
  -webkit-animation: fadeIn ease-in 1;
  -moz-animation: fadeIn ease-in 1;
  animation: fadeIn ease-in 1;
  -webkit-animation-fill-mode: forwards;
  -moz-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
  -webkit-animation-duration: .5s;
  -moz-animation-duration: .5s;
  animation-duration: .5s;
}
.swing-right-fwd {
  -webkit-animation: swing-right-fwd 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  animation: swing-right-fwd 0.4s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}
